<template>
    <div>
        <top-header :active="'5'"></top-header>
        <div class="top"></div>
        <!-- 大盒子的让内容在中间1200px内显示 -->
        <!-- <button @click="Fnknowledgelist()">获取知识库</button> -->
        <div class="dynamicBox">
            <!-- 文章列表排版 -->
            <div class="dynamicList" v-for="(item,index) in DdynamicList" :key="index" @click="Fngitodetails(item.k_id)">
                <!-- <img :src="item.fengimg" alt=""> -->
                <div class="dynamicList_text">
                    <h4>
                        {{item.k_title}}
                    </h4>
                    <p>
                        {{item.k_content | rephtml}}
                    </p>
                    <div class="time">
                        <svg class="icon">
                            <use xlink:href="#icon-rili"></use>
                        </svg>
                        {{item.k_date | toTime}}
                    </div>
                </div>
            </div>
            <!-- 文章列表排版 -->
        </div>
        <el-pagination
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        @current-change="handleCurrentChange"
        :page-size="PageSize"
        :total="total">
        </el-pagination>
        <div class="footer">
            © 黄岛达摩智联科技有限公司版权所有 浙ICP备15005406号-1
        </div>
    </div>
</template>
<script>
import topHeader from './Header'
import url from "../../common/api";
export default {
    data() {
        return {
            DdynamicList:[
                {
                    fengimg:require('../../assets/img/banner4.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner2.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透'
                },
                {
                    fengimg:require('../../assets/img/banner3.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner4.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner2.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner3.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner2.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
                {
                    fengimg:require('../../assets/img/banner3.jpg'),
                    tit:'达摩为中国联通提供 MEC 边缘计算物联网能力模块',
                    info:'达摩支撑中国联通将信息数字化转型升级向各行各业融合渗透',
                    time:'2019年12月1日'
                },
            ],
            total:0,//分页-总条目
            currentPage:1,//分页-当前页数
            PageSize:20,//每页有多少条数
        };
    },
    methods:{
        Fnknowledgelist(){//查询知识库列表
            url.knowledgelist({
                page:this.currentPage,
                per_page:this.PageSize,//每页显示条数
            })
            .then(res=>{
                console.log(res,'--------知识库查询结果')
                if(res.data.code[0]='200'){
                    let xx=res.data.data
                    let newlist=[]
                    xx.forEach(element => {
                        element.fields.k_id=element.pk
                        newlist.push(element.fields)
                    });
                    this.DdynamicList=newlist
                    this.total=res.data.datacount
                }
            })
        },
        handleCurrentChange(val) {//点击分页功能实现数据切换
            this.currentPage=val
            this.Fnknowledgelist()
        },
        Fngitodetails(k_id){//去详情页面
            this.$router.push({
                path:'/articleDetails', //也可以在路径上拼接
                query:{
                k_id:k_id,      //传递的值
                k_type:'知识库'
                }
            })
        },

    },
    components:{
        topHeader
    },
    mounted(){
        this.Fnknowledgelist()
    }
}
</script>
<style lang='stylus' scoped>
    .top
        height 92px
    .dynamicBox
        width 1200px
        margin 50px auto 30px
        overflow hidden
        .dynamicList
            width 350px
            height 180px
            box-shadow: 0px 8px 12px 2px #ccc;
            float left
            margin 20px 25px
            img 
                width 350px
                height 200px
                display block
            .dynamicList_text
                padding 25px
                h4 
                    font-weight bold
                    font-size 16px
                    color #666
                    line-height 1.3
                    margin-right 8px
                    overflow:hidden; 
                    text-overflow:ellipsis;
                    display:-webkit-box; 
                    -webkit-box-orient:vertical;
                    -webkit-line-clamp:2; 
                h4:hover
                    color #292dc2
                p 
                    color: #999;
                    font-weight: 400;
                    font-size 15px
                    line-height: 170%;
                    padding 0px 0
                    border-bottom 1px dashed #CEE1F8
                    overflow:hidden; 
                    text-overflow:ellipsis;
                    display:-webkit-box; 
                    -webkit-box-orient:vertical;
                    -webkit-line-clamp:2; 
                .time
                    color #6f6f6f
                    font-size: 16px;
                    margin-top: 24px;
    .footer
        background rgb(1,21,54)
        line-height 60px
        color #ffffff
        text-align center
        margin-top 50px
</style>


